:root {
  --red: #FF0000;
  --blue: #0F4083;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 8px;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right bottom, from(var(--blue)), to(var(--red)));
  background: -webkit-linear-gradient(top left, var(--blue), var(--red));
  background: -o-linear-gradient(top left, var(--blue), var(--red));
  background: linear-gradient(to bottom right, var(--blue), var(--red));
  height: 100%;
  color: white;
  min-height: 100%;
}



